.Slider {
  height: 525px;
  opacity: 0.75;
  position: relative;
}

/* -----------------
    Product Information
    -------------------- */

.productInfo {
  height: 100%;
  position: absolute;
  top: 0;
  left: 9.7%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.productInfo h1 {
  font-size: 3.5rem;
  font-weight: 700;
}

.productInfo p {
  font-size: 1.7rem;
  font-weight: 500;
  color: var(--grey-dark);
  margin: 10px 0 20% 0;
}

/* -----------------
    Product Image
    -------------------- */

.productImage {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  z-index: -1;

  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.productImage img {
  width: 67%;
  height: auto;
}

/* -----------------
    Products Navigation 
    -------------------- */

.navContainer {
  width: 100px;
  height: 20px;
  position: absolute;
  bottom: 50px;
  left: 50%;
  transform: translateX(-50%);

  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.navContainer span {
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background-color: var(--blue-supertransparent);
  cursor: pointer;

  transition: background-color 0.1s ease-out;
}

.navContainer span:hover {
  background-color: var(--blue-semitransparent);
}

.navContainer span.active {
  background-color: var(--blue);
}
