.BestProduct {
  width: 567px;
  height: 313px;
  max-width: 90%;
  border: solid 1px var(--blue-supertransparent);
  overflow: hidden;
  border-radius: 5px;

  transition: border 0.2s ease-out;

  display: flex;
  justify-content: space-between;
  align-items: stretch;
}

.BestProduct:hover {
  border: solid 1px var(--blue);
}

.productImage {
  overflow: hidden;

  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.productImage img {
  width: 100%;
  height: auto;
  cursor: pointer;
}

.productInfo {
  flex-grow: 1;

  padding: 30px 15px;
}

.productInfo > a {
  text-decoration: none;
  color: black;
}

.title {
  font-size: 1.3rem;
  font-weight: 700;
  line-height: 170%;
  cursor: pointer;

  transition: color 0.1s ease-out;
}

.title:hover {
  color: var(--blue);
}

.details {
  font-size: 1.1rem;
  font-weight: 400;
  color: var(--grey-dark);
  margin-bottom: 75px;
  margin-top: 10px;
  line-height: 140%;
}

.price {
  font-size: 1.2rem;
  font-weight: 700;
  margin-bottom: 10px;
}

.availability {
  font-size: 1rem;
  font-weight: 300;
  color: var(--grey-dark);
  margin-top: 5px;
}

.availability strong {
  color: var(--blue);
}

/* ==========================
    Setting it for normal product
    ============================== */

.NormalProduct {
  height: 400px;
  width: 260px;
  max-width: 90%;
  border: solid 1px var(--blue-supertransparent);
  overflow: hidden;
  border-radius: 5px;

  transition: border 0.2s ease-out;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: stretch;
}

.NormalProduct:hover {
  border: solid 1px var(--blue);
}

.NormalProduct .productImage {
  width: 100%;
}

.NormalProduct .productImage img {
  width: 100%;
  height: auto;
}

.NormalProduct .productInfo {
  text-align: center;
}

.NormalProduct .title {
  font-size: 1.1rem;
  margin-bottom: 40px;
}

.NormalProduct .details {
  display: none;
  margin-bottom: 0;
}
