.Search {
  height: 50px;
  background-color: var(--blue-lightened);
  border-radius: 25px;

  display: flex;
  justify-content: space-between;
  align-items: stretch;
}

.Search a {
  text-decoration: none;

  display: flex;
  justify-content: stretch;
  align-items: stretch;
}

.searchIcon {
  width: 50px;
  border-radius: 25px 0 0 25px;
  cursor: pointer;

  display: flex;
  justify-content: center;
  align-items: center;
}

.searchInput {
  width: 0;
  /* width: 250px; */
  border: none;
  outline: none;
  background-color: transparent;
  border-radius: 0 25px 25px 0;
  color: white;
  font-size: 1.1rem;
  font-weight: 400;

  transition: width 0.2s ease-out, padding 0.2s ease-out;
}

.searchInput::placeholder {
  color: var(--white-semitransparent);
}

.active .searchInput {
  width: 250px;
}
