.Header {
  height: 100px;
  background-color: var(--blue);
  padding: 0 7.32%;
  color: white;
  top: 0;
  left: 0;
  z-index: 999;

  transition-property: position, height, background-color;
  transition-duration: 0.3s;
  transition-timing-function: ease-out;

  /* Layout */
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.headerScrolled {
  position: sticky;

  height: 70px;
  background-color: var(--blue-semitransparent);
}

.title {
  font-size: 1.6rem;
  font-weight: 600;
  letter-spacing: 0.3px;
  cursor: pointer;
}

.title > a {
  text-decoration: none;
  color: white;
}

.navigation {
  /* Layout */
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.navigation > * {
  margin: 0 15px;
  text-decoration: none;
}

.navigation i {
  color: white;
  font-size: 25px;
  cursor: pointer;
}
